/* BACKGROUND AND BASIC WRAPPER */
body {
  background-color: #f1eaea;
  background-image: url("../images/flinders.jpg");
  overflow-x: hidden;
  height: auto;
  overflow-y: scroll;
}

.about-wrapper {
  margin-top: 150px;
  height: 100%;
}

/* GENERAL DESCRIPTION SECTION */

.about-paragraph {
  background-color: black;
  opacity: 0.8;
  height: 400px;
  padding-left: 6%;
  width: 100%;
  color: white;
}

p {
  padding: 4% 4% 4% 4%;
  width: 60%;
}

/* VERDICT BUTTON  */
.results-button {
  background-color: #d6761b; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin-left: 4%;
}
.results-button:hover,
.results-button:active {
  background-color: #351d06; /* Green */
  cursor: pointer;
}

/* MACHINE LEARNING DESCRIPTION SECTION */

.ml-paragraph {
  background-color: #351d06;
  margin-top: 100px;
  height: 300px;
  padding-left: 6%;
  width: 100%;
  color: white;
}

/* PHONE LAYOUT */
@media (max-width: 820px) {
  /* MAIN WRITING */
  p {
    padding-top: 40px;
    width: 80%;
  }

  .about-paragraph {
    height: 450px;
  }
}
